import React, { useContext } from 'react';
import './style.css';
import { ContentContext } from '../../App';
var { DateTime } = require('luxon');
var monthNames = ['JANUARY','FEBRUARY','MARCH','APRIL','MAY','JUNE','JULY','AUGUST','SEPTEMBER','OCTOBER','NOVEMBER','DECEMBER'];


function Footer({props}) {
    const month = monthNames[DateTime.now().c.month];
    console.log(props);
    const setContent = useContext(ContentContext);
    let buttonType = <button className="addHabitButton" onClick={()=>{setContent(1)}}>+</button>;
    if (props !== 0) {
        buttonType = <button className="closeHabitButton" onClick={()=>{setContent(0)}}>X</button>;
    } else {
        buttonType = <button className="addHabitButton" onClick={()=>{setContent(1)}}>+</button>;
    }
    return (
            <div className="footer">
                
                <div className="addHabit">
                    {buttonType}
                </div>
                <hr className="MenuLine"></hr>
                <h4 className="time">{DateTime.now().c.hour}:{DateTime.now().c.minute}</h4>
                <h6 className="date">{month} {DateTime.now().c.day}</h6>
            </div>
    )
}

export default Footer;
